import './App.css';
import { useRef,useState,useEffect } from "react"
import Navbar from './components/Navbar';
import Slideshowlarge from './components/Slideshowlarge';
import Gallery from './components/Gallery';
import Productfeatures from './components/Productfeatures';
import Footer from './components/Footer';
import ScratchProjects from './components/ScratchProjects';
function App() {
  const galleryref=useRef(null);
  const coursesref=useRef(null);
  const comminsoonref=useRef(null);
  const [prodselected,prodselected1]=useState(0);
  useEffect(()=>{
    document.querySelectorAll(".slide.scratch").forEach((element,index)=>{
      element.addEventListener('click', function () {
        prodselected1(index);
          console.log(document.querySelectorAll(".slide.scratch"))
         })
  })
  },[])
  

  return (
    <div className="App">
      <header className="App-header">
        
      </header>
      <Navbar class='Navbar' sendData1={(data) => {if(data[0]){galleryref.current?.scrollIntoView() } else if(data[3]){coursesref.current?.scrollIntoView() } else if(data[4]){comminsoonref.current?.scrollIntoView() }}} ></Navbar>
      <Slideshowlarge ></Slideshowlarge>
      <section ref={galleryref}>
      <Gallery ></Gallery>
      </section>
      <section ref={coursesref}>
      <Productfeatures></Productfeatures>
      </section>
      <section ref={comminsoonref}>
        <ScratchProjects prodselected={prodselected}></ScratchProjects>
      </section>
      <section>
      <Footer></Footer>
      </section>
      
      
    </div>
  );
}

export default App;