import React from "react";
import './css/Productslideshow.css';
import $ from 'jquery'
import data from '../assets/scratch/scratchdata.json';
import Container from 'react-bootstrap/Container';
function importAll(r) {
    return r.keys().map(r);
}

const images = importAll(require.context('../assets/scratch/', false, /\.(png|jpe?g|svg|webp)$/));
// const stars = importAll(require.context('../assets/stars/', false, /\.(svg)$/));
const Slide = (season, index, sele) => {
    return <div className={`slide${sele} scratch`} style={{ order: index }}>
        <div class='box'>
            <img id='prodimg' className='borderradius' src={images[index]} alt={`img${index}`} />
            <div class='descriptions'>
                {/* <p>{season.price}</p> */}
                <div class='description'>
                    <p>{season.title}</p>
                </div>
            </div>
        </div>
    </div>;
}

class ScratchProjects extends React.Component {
    constructor(props) {
        super(props);
        this.state = {prodselected:1};

    }
    render() {
        return (
            <Container className="scratch">
                <div class='productss'>
                    <div class='cardbox cardboxscratch'>
                        <div class='autolayout'>
                            {data.map((season, index) => (
                                <>
                                    {index === this.props.prodselected ?
                                        Slide(season, index, ' select')
                                        :
                                        Slide(season, index, '')}

                                </>
                            ))}
                        </div>
                    </div>
                    <svg class='rightarrowpro rightarrowproscratch' width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <g clip-path="url(#clip0_1260_2423)">
                            <path d="M27 15L12 -1.90735e-06L10.5 1.5L24 15L10.5 28.5L12 30L27 15Z" fill="black" />
                        </g>
                        <defs>
                            <clipPath id="clip0_1260_2423">
                                <rect width="30" height="30" fill="white" />
                            </clipPath>
                        </defs>
                    </svg>
                    <svg class='leftarrowpro leftarrowproscratch' width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M3 15L18 30L19.5 28.5L6 15L19.5 1.5L18 0L3 15Z" fill="black" />
                    </svg>

                </div>
                <Container className="scratchviewcontainer">
                <iframe src={`https://scratch.mit.edu/projects/${data[this.props.prodselected].code}/embed`} allowtransparency="true" width="485" height="402" frameborder="0" scrolling="no" allowfullscreen></iframe>
                </Container>
                
            </Container>
        )
    }
    jQuerycode = () => {
        $('.leftarrowproscratch').on('click', function () {
            $('.cardboxscratch').animate({
                scrollLeft: '-=224'
            }, 300, 'swing');
        });

        $('.rightarrowproscratch').on('click', function () {
            $('.cardboxscratch').animate({
                scrollLeft: '+=224'
            }, 300, 'swing');
        });
    }
    componentDidMount() {
        this.jQuerycode()
        
    }
}
export default ScratchProjects;