import React from "react";
import './css/Productslideshow.css';
import $ from 'jquery'
import data from '../assets/products/data.json';
function importAll(r) {
    return r.keys().map(r);
}

const images = importAll(require.context('../assets/products/', false, /\.(png|jpe?g|svg)$/));
const stars = importAll(require.context('../assets/stars/', false, /\.(svg)$/));
const Slide = (season, index, sele) => {
    return <div className={`slide${sele} product`} style={{ order: index }}>
        <div class='box'>
            <img id='prodimg' src={images[index]} alt={`img${index}`} />
            <div class='descriptions'>
                <p>{season.price}</p>
                <div class='description'>
                    <p>{season.title}</p>
                    <div class='rate'>
                        <p>{season.reviews}</p>
                        <img class='first' src={stars[season.star1]} alt="starfill"></img>
                        <img class='second' src={stars[season.star2]} alt="starfill"></img>
                        <img class='third' src={stars[season.star3]} alt="starfill"></img>
                        <img class='fourth' src={stars[season.star4]} alt="starfill"></img>
                        <img class='fifth' src={stars[season.star5]} alt="starhalf"></img>
                    </div>
                </div>
            </div>
        </div>
    </div>;
}
class Productslideshow extends React.Component {
    render() {
        return (
            <div class='productss'>
                <div class='cardbox cardboxproduct'>
                    <div class='autolayout'>
                        {data.map((season, index) => (
                            <>
                                {index === this.props.prodselected ?
                                    Slide(season, index, ' select')
                                    :
                                    Slide(season, index, '')}

                            </>
                        ))}
                    </div>
                </div>
                <svg class='rightarrowpro rightarrowproproduct' width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <g clip-path="url(#clip0_1260_2423)">
                        <path d="M27 15L12 -1.90735e-06L10.5 1.5L24 15L10.5 28.5L12 30L27 15Z" fill="black" />
                    </g>
                    <defs>
                        <clipPath id="clip0_1260_2423">
                            <rect width="30" height="30" fill="white" />
                        </clipPath>
                    </defs>
                </svg>
                <svg class='leftarrowpro leftarrowproproduct' width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M3 15L18 30L19.5 28.5L6 15L19.5 1.5L18 0L3 15Z" fill="black" />
                </svg>

            </div>
        )
    }
    componentDidMount() {

        $('.leftarrowproproduct').on('click', function () {
            $('.cardboxproduct').animate({
                scrollLeft: '-=224'
            }, 300, 'swing');
        });

        $('.rightarrowproproduct').on('click', function () {
            $('.cardboxproduct').animate({
                scrollLeft: '+=224'
            }, 300, 'swing');
        });
    }
}
export default Productslideshow;