import React from 'react';
import { toString, spring } from "css-spring";
import './css/Slideshowlarge.css'
import image from '../assets/image.png'
import image2 from '../assets/image2.png'
import image3 from '../assets/image3.png'

class Slideshowlarge extends React.Component {
    constructor(props) {
        super(props);
        this.state = {bounceKeyframes:'',bounceKeyframes2:'',bounceKeyframes3:'',bounceKeyframes4:'',bounceKeyframes5:'',bounceKeyframes6:'',offsetXdown2:0,offsetXdown1:0,scrouledwidth:0,offsetXdown:0, isPrsessdDown:false,anim:0,forward: true, i: 0, width: document.body.clientWidth, height: document.body.clientHeight, imgheight: (document.body.clientWidth / 1200) * 500 };

    }

    render() {
        let slides = [' third ', ' fourth', ' fifth'];
        let slidesbackclick = [' fourthbackclick ', ' secondbackclick ', ' thirdbackclick '];
        let color = ['  ',' white ', ' white ']
        let activeslide = slides[this.state.i];
        let activecolor = color[this.state.i];
        let third ='';
        let fourth = '';
        let fifth = '';
        if (this.state.i===0){third=' third '}else {third=''}
        if (this.state.i===1){fourth=' fourth '}else {fourth=''}
        if (this.state.i===2){fifth=' fifth '}else {fifth=''}
        if (!this.state.forward) { activeslide =  slidesbackclick[this.state.i] } 
        else if (this.state.forward){activeslide = slides[this.state.i]}
        
         


        return (
            <div class='Slidecontainer' draggable='false'>
                <style>{`@keyframes bounce {${this.state.bounceKeyframes}}
                @keyframes bounce2 {${this.state.bounceKeyframes2}}
                @keyframes bounce3 {${this.state.bounceKeyframes3}}
                @keyframes bounce4 {${this.state.bounceKeyframes4}}
                @keyframes bounce5 {${this.state.bounceKeyframes5}}
                @keyframes bounce6 {${this.state.bounceKeyframes6}}
                `}</style>
            <div class='Slideshowlarge'  draggable='false' >

                <div className={'slides ' + activeslide }  draggable='false'>
                    <div class='slide first'  draggable='false' style={{ order: 0, width: this.state.width }}>
                    
                        <img src={image3} alt='image3'></img>
                    </div>
                    <div class='slide second'  draggable='false' style={{ order: 1, width: this.state.width }}>
                    
                    <div class='absolute'>
                    <img src={image} alt='image1'  draggable='false'></img>
                    <div className={'containerbox'+third}>
                            <div class='textbox'>
                                <p class='title'>ARDUINO GIGA R1 WiFi</p>
                                <p class='discription'>Step up your game!</p>
                            </div>
                            <button class='discovermore'>Discover More</button>
                            <button class='buynow'>Buy Now</button>
                        </div> 
                    </div>
       
                    
                        
                    </div>
                    <div class='slide third'  draggable='false' style={{ order: 2, width: this.state.width }}>
                    <div class='absolute'>
                    <img src={image2} alt='image2' draggable='false'></img>
                    <div className={'containerbox'+fourth}>
                            <div class='textbox'>
                                <p class='title second'>Introducing Arduino Opta: micro PLC with Industrial IoT capabilities</p>
                                <p class='titlemini '>Arduino Opta</p>
                                <p class='discription second'>Put it to work on your next industrial and building automation project</p>
                                <p class='discriptionmini'>Secure, easy-to-use micro PLC with industrial IoT capabilities</p>
                            </div>
                            <button class='discovermore second'>Discover More</button>
                            
                        </div> 
                    </div>
                    </div>
                    <div class='slide fourth'  draggable='false' style={{ order: 3, width: this.state.width }}>
                    <div class='absolute'>
                    <img src={image3} alt='image3' draggable='false'></img>
                    <div className={'containerbox'+fifth}>
                            <div class='textbox'>
                                <p class='title third'>Nicla Voice</p>
                                <p class='discription third'>Always-on, voice-triggered solutions on the edge</p>
                            </div>
                            <button class='buynow third'>Buy Now</button>
                        </div> 
                    </div>
                    </div>
                    <div class='slide fifth'  draggable='false' style={{ order: 4, width: this.state.width }}>
                    {/* <img src={image} alt='image1'></img> */}
                    <div class='absolute'>
                    <img src={image} alt='image1'  draggable='false'></img>
                    <div className={'containerbox' }>
                            <div class='textbox'>
                                <p class='title'>ARDUINO GIGA R1 WiFi</p>
                                <p class='discription'>Step up your game!</p>
                            </div>
                            <button class='discovermore'>Discover More</button>
                            <button class='buynow'>Buy Now</button>
                        </div> 
                    </div>

  
                    </div>

                </div>
                <svg type='button' onClick={this.leftclick} className={'leftarrow ' + activecolor}  width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M3 15L18 30L19.5 28.5L6 15L19.5 1.5L18 0L3 15Z" fill="black" />
                </svg>
                

                <svg onClick={this.rightclick} className={'rightarrow ' + activecolor} width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <g clip-path="url(#clip0_1058_11893)">
                        <path d="M27 15L12 -1.90735e-06L10.5 1.5L24 15L10.5 28.5L12 30L27 15Z" fill="black" />
                    </g>
                    <defs>
                        <clipPath id="clip0_1058_11893">
                            <rect width="30" height="30" fill="white" />
                        </clipPath>
                    </defs>
                </svg>
                

                <div class='navigation'>
                    <div class='navigation-box' >
                        <div className={'rectangle ' + this.active(0) + activecolor} style={{ order: 0 }}></div>
                        <div className={'rectangle ' + this.active(1) + activecolor} style={{ order: 1 }}></div>
                        <div className={'rectangle ' + this.active(2) + activecolor} style={{ order: 2 }}></div>

                    </div>

                </div>
            </div>
            </div>

        )


    }
    updateDimensions = () => {
        this.setState({ width: document.body.clientWidth, height: document.body.clientHeight, imgheight: (document.body.clientWidth / 1200) * 500 });
    };

    active = (i) => {
        if (this.state.i === i) { return ' active' }
        if (this.state.i === i) { return '' }
    }

    rightclick = () => {
      
        console.log(this.state.i);
        this.setState({ forward: true });
        let Slides = ['third', 'fourth', 'fifth'];
        let slidesclick = ['thirdclick', 'fourthclick', 'fifthclick'];
        const slides = document.querySelector('.slides')
          slides.classList.replace(Slides[this.state.i],slidesclick[this.state.i])
    }
    leftclick = () => {
        if (this.state.i ===2) {
            this.setState({timedown: this.state.looptime, forward: false, runmyloop: false });
            this.setState({ i: 0 });
        }
        else if (this.state.i ===1) {
            this.setState({forward: false})
            this.setState({ i: 2 })
        }
        else if (this.state.i ===0) {
            this.setState({forward: false})
            this.setState({ i: 1 })
        }
        console.log(this.state.i)
        this.setState({ forward: false });
        let Slides = ['third', 'fourth', 'fifth'];
        let slidesbackclick = [ 'thirdbackclick','fourthbackclick','secondbackclick' ];
        const slidess = document.querySelector('.slides')
          slidess.classList.replace(Slides[this.state.i],slidesbackclick[this.state.i])
    }
    componentDidMount() {
        const fullnavbar = document.querySelector('.fullnavbar')
        this.setState({ width: fullnavbar.clientWidth, height: window.innerHeight, imgheight: (document.body.clientWidth / 1200) * 500 });
        window.addEventListener('resize', this.updateDimensions)
        window.addEventListener('resize', ()=>{
            if (this.state.i < 2 ) {
                slides.style.left= `${-(this.state.width*(this.state.i+1)+this.state.scrouledwidth)}px`;
            }
            if (this.state.i > 1) {
                slides.style.left= `${-(this.state.width*3+this.state.scrouledwidth)}px`;
            }
            
        })
        

        const container = document.querySelector('.Slidecontainer')
        const slides = document.querySelector('.slides')
        slides.style.left= `${-(container.clientWidth*(this.state.i+1))}px`;
        this.setState({ anim: this.state.anim+1 })
        this.setState({bounceKeyframes:toString(
            spring(
              {
                left: `${-(container.clientWidth)}`
              },
              {
                left: `${-(container.clientWidth)*2}`
              },
              { preset: 'gentle'} // default configuration
            ),
            (property, value) => `${property}:${value}px;`
          )})
          this.setState({bounceKeyframes2:toString(
            spring(
              {
                left: `${-(container.clientWidth*2)}`
              },
              {
                left: `${-(container.clientWidth)*3}`
              },
              { preset: 'gentle'} // default configuration
            ),
            (property, value) => `${property}:${value}px;`
          )})
          this.setState({bounceKeyframes3:toString(
            spring(
              {
                left: `${-(container.clientWidth)*3}`
              },
              {
                left:`${-(container.clientWidth*4)}` 
              },
              { preset: 'gentle'} // default configuration
            ),
            (property, value) => `${property}:${value}px;`
          )});
          this.setState({bounceKeyframes4:toString(
            spring(
              {
                left: `${-(container.clientWidth)}`
              },
              {
                left: `${0}`
              },
              { preset: 'gentle'} // default configuration
            ),
            (property, value) => `${property}:${value}px;`
          )});
          this.setState({bounceKeyframes5:toString(
            spring(
              {
                left: `${-(container.clientWidth)*2}` 
              },
              {
                left: `${-(container.clientWidth)}`
              },
              { preset: 'gentle'} // default configuration
            ),
            (property, value) => `${property}:${value}px;`
          )})
          this.setState({bounceKeyframes6:toString(
            spring(
              {
                left: `${-(container.clientWidth)*3}`
              },
              {
                left: `${-(container.clientWidth*2)}`
              },
              { preset: 'gentle'} // default configuration
            ),
            (property, value) => `${property}:${value}px;`
          )});
          
        slides.addEventListener("animationend", () => {
                if (this.state.i < 2 ) {
                    this.setState({ forward: true });
                    this.setState({ i: this.state.i + 1 });
                    slides.style.left= `${-(this.state.width*(this.state.i+2)+this.state.scrouledwidth)}px`;  
                }
                if (this.state.i > 1) {
                    this.setState({ forward: true })
                    this.setState({ i: 0 })
                    slides.style.left= `${-(this.state.width+this.state.scrouledwidth)}px`;
                }
                this.setState({bounceKeyframes:toString(
                  spring(
                    {
                      left: `${-(container.clientWidth)}`
                    },
                    {
                      left: `${-(container.clientWidth)*2}`
                    },
                    { preset: 'gentle'} // default configuration
                  ),
                  (property, value) => `${property}:${value}px;`
                )})
                this.setState({bounceKeyframes2:toString(
                  spring(
                    {
                      left: `${-(container.clientWidth*2)}`
                    },
                    {
                      left: `${-(container.clientWidth)*3}`
                    },
                    { preset: 'gentle'} // default configuration
                  ),
                  (property, value) => `${property}:${value}px;`
                )})
                this.setState({bounceKeyframes3:toString(
                  spring(
                    {
                      left: `${-(container.clientWidth)*3}`
                    },
                    {
                      left:`${-(container.clientWidth*4)}` 
                    },
                    { preset: 'gentle'} // default configuration
                  ),
                  (property, value) => `${property}:${value}px;`
                )});
                this.setState({bounceKeyframes4:toString(
                  spring(
                    {
                      left: `${-(container.clientWidth)}`
                    },
                    {
                      left: `${0}`
                    },
                    { preset: 'gentle'} // default configuration
                  ),
                  (property, value) => `${property}:${value}px;`
                )});
                this.setState({bounceKeyframes5:toString(
                  spring(
                    {
                      left: `${-(container.clientWidth)*2}` 
                    },
                    {
                      left: `${-(container.clientWidth)}`
                    },
                    { preset: 'gentle'} // default configuration
                  ),
                  (property, value) => `${property}:${value}px;`
                )})
                this.setState({bounceKeyframes6:toString(
                  spring(
                    {
                      left: `${-(container.clientWidth)*3}`
                    },
                    {
                      left: `${-(container.clientWidth*2)}`
                    },
                    { preset: 'gentle'} // default configuration
                  ),
                  (property, value) => `${property}:${value}px;`
                )});
                
          },false);
          let cursorXSpace
            container.addEventListener("mousedown",(e)=>{
                this.setState({ isPrsessdDown: true });
                cursorXSpace = (e.offsetX) - slides.offsetLeft;
                this.setState({offsetXdown:e.offsetX})
                // console.loge(slides.offsetLeft)
            });
            container.addEventListener("touchstart",(e)=>{
                this.setState({ isPrsessdDown: true });
                cursorXSpace = (e.touches[0].pageX) - slides.offsetLeft;
                this.setState({offsetXdown1:e.touches[0].pageX})
                // console.loge(slides.offsetLeft)
            });
         ["mouseup","mousleave"].forEach((event) => {
            container.addEventListener(event,(e)=>{
                this.setState({isPrsessdDown:false})
                this.setState({scrouledwidth:0})
                if((this.state.offsetXdown>e.offsetX)){
                    this.rightclick()
                }
                
                else if((this.state.offsetXdown<e.offsetX)){
                    this.leftclick()
                }
            },false)
         });
         container.addEventListener("touchend",(e)=>{
            this.setState({isPrsessdDown:false})
            this.setState({scrouledwidth:0})
            if((this.state.offsetXdown1>this.state.offsetXdown2)){
                this.rightclick()
            }
            else if((this.state.offsetXdown1<this.state.offsetXdown2)){
              this.leftclick()
            }
        },false)
            container.addEventListener("touchmove",(e)=> {
                if (!this.state.isPrsessdDown)return;
                e.preventDefault();
                slides.style.left = `${(e.touches[0].pageX) - cursorXSpace}px`
                this.setState({scrouledwidth:(e.touches[0].pageX) - cursorXSpace})
                this.setState({offsetXdown2:e.touches[0].pageX})
                this.setState({bounceKeyframes:toString(
                    spring(
                      {
                        left: `${-(container.clientWidth+(this.state.offsetXdown1-this.state.offsetXdown2))}`
                      },
                      {
                        left: `${-(container.clientWidth)*2}`
                      },
                      { preset: 'gentle'} // default configuration
                    ),
                    (property, value) => `${property}:${value}px;`
                  )})
                  this.setState({bounceKeyframes2:toString(
                    spring(
                      {
                        left: `${-(container.clientWidth*2+(this.state.offsetXdown1-this.state.offsetXdown2))}`
                      },
                      {
                        left: `${-(container.clientWidth)*3}`
                      },
                      { preset: 'gentle'} // default configuration
                    ),
                    (property, value) => `${property}:${value}px;`
                  )})
                  this.setState({bounceKeyframes3:toString(
                    spring(
                      {
                        left: `${-(container.clientWidth*3+(this.state.offsetXdown1-this.state.offsetXdown2))}`
                      },
                      {
                        left: `${-(container.clientWidth)*4}`
                      },
                      { preset: 'gentle'} // default configuration
                    ),
                    (property, value) => `${property}:${value}px;`
                  )})
                  this.setState({bounceKeyframes4:toString(
                    spring(
                      {
                        left: `${-(container.clientWidth+(this.state.offsetXdown1-this.state.offsetXdown2))}`
                      },
                      {
                        left: `${0}`
                      },
                      { preset: 'gentle'} // default configuration
                    ),
                    (property, value) => `${property}:${value}px;`
                  )});
                  this.setState({bounceKeyframes5:toString(
                    spring(
                      {
                        left: `${-(container.clientWidth*2+(this.state.offsetXdown1-this.state.offsetXdown2))}` 
                      },
                      {
                        left: `${-(container.clientWidth)}`
                      },
                      { preset: 'gentle'} // default configuration
                    ),
                    (property, value) => `${property}:${value}px;`
                  )})
                  this.setState({bounceKeyframes6:toString(
                    spring(
                      {
                        left: `${-(container.clientWidth*3+(this.state.offsetXdown1-this.state.offsetXdown2))}`
                      },
                      {
                        left: `${-(container.clientWidth*2)}`
                      },
                      { preset: 'gentle'} // default configuration
                    ),
                    (property, value) => `${property}:${value}px;`
                  )});
            });
         container.addEventListener('mousemove',(e)=> {
            if (!this.state.isPrsessdDown)return;
            e.preventDefault();
            slides.style.left = `${(e.offsetX) - cursorXSpace}px`
            this.setState({scrouledwidth:(e.offsetX) - cursorXSpace})
            // console.log(-this.state.scrouledwidth)
            this.setState({bounceKeyframes:toString(
              spring(
                {
                  left: `${-(container.clientWidth+(this.state.offsetXdown-e.offsetX))}`
                },
                {
                  left: `${-(container.clientWidth)*2}`
                },
                { preset: 'gentle'} // default configuration
              ),
              (property, value) => `${property}:${value}px;`
            )})
            this.setState({bounceKeyframes2:toString(
              spring(
                {
                  left: `${-(container.clientWidth*2+(this.state.offsetXdown-e.offsetX))}`
                },
                {
                  left: `${-(container.clientWidth)*3}`
                },
                { preset: 'gentle'} // default configuration
              ),
              (property, value) => `${property}:${value}px;`
            )})
            this.setState({bounceKeyframes3:toString(
              spring(
                {
                  left: `${-(container.clientWidth*3+(this.state.offsetXdown-e.offsetX))}`
                },
                {
                  left: `${-(container.clientWidth)*4}`
                },
                { preset: 'gentle'} // default configuration
              ),
              (property, value) => `${property}:${value}px;`
            )})
            this.setState({bounceKeyframes4:toString(
              spring(
                {
                  left: `${-(container.clientWidth+(this.state.offsetXdown-e.offsetX))}`
                },
                {
                  left: `${0}`
                },
                { preset: 'gentle'} // default configuration
              ),
              (property, value) => `${property}:${value}px;`
            )});
            this.setState({bounceKeyframes5:toString(
              spring(
                {
                  left: `${-(container.clientWidth*2+(this.state.offsetXdown-e.offsetX))}` 
                },
                {
                  left: `${-(container.clientWidth)}`
                },
                { preset: 'gentle'} // default configuration
              ),
              (property, value) => `${property}:${value}px;`
            )})
            this.setState({bounceKeyframes6:toString(
              spring(
                {
                  left: `${-(container.clientWidth*3+(this.state.offsetXdown-e.offsetX))}`
                },
                {
                  left: `${-(container.clientWidth*2)}`
                },
                { preset: 'gentle'} // default configuration
              ),
              (property, value) => `${property}:${value}px;`
            )});
        })  
           
        
            
        
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions)
        const slides = document.querySelector('.slides')
        slides.removeEventListener("animationstart", () => {
            
            console.log('animation has started');
          });}  
}
export default Slideshowlarge;